import React, { useState } from "react";

import "./style.scss";
import informationIcon from "../../assets/images/information-icon.jpg";
import arrowDown from "../../assets/images/arrow-down.png";
import arrowUp from "../../assets/images/arrow-up.png";

const News = (props) => {
  const { newsTitle, newsDesc, newsLink } = props;
  const [isDropDown, setIsDropDown] = useState(false);
  const descClass = isDropDown ? "desc-show" : "desc-hide";

  return (
    <div className="news">
      <div className="alert inner">
        <img className="information" src={informationIcon} alt="" />
        <span className="alert-title">{newsTitle}</span>
        <div style={{cursor: "pointer"}} onClick={()=> setIsDropDown(!isDropDown)}>
          <img className="arrow" src={isDropDown ? arrowUp : arrowDown} alt="" />
        </div>
      </div>
      <div className="desc inner">
        <div className={descClass}>
          <p className="alert-text">{newsDesc}</p>
          <p className="alert-link">
            <a className="" href={newsLink}>Read more</a>
          </p>
        </div>
      </div>
    </div>
  );
}

export default News;
