import React, { useEffect, useRef, useState } from "react";

import "./style.scss";
import PeopleIcon from "../../assets/images/team.svg";
import UpAndDownIcon from "../../assets/images/up-and-down.png";


const GuestInput = () => {
  const guestInputRef = useRef();
  const [adult, setAdult] = useState(0);
  const [children, setChildren] = useState(0);
  const [room, setRoom] = useState(0);
  const [isOpen, setIsOpen] = useState(true);

  const handleClickOutsideBox = e => {
    if (!guestInputRef.current.contains(e.target)) {
      setIsOpen(true);
    }
  };

  const handleClickInsideBox = () => {
    setIsOpen(false);
  }

  useEffect(() => {
    document.addEventListener("mousedown", handleClickOutsideBox);
    return () => document.removeEventListener("mousedown", handleClickOutsideBox);
  });
  
  return (
    <div ref={guestInputRef} onClick={() => handleClickInsideBox()} className="guest-wrapper">
      <img className="customer-icon" src={PeopleIcon} alt="" />

      <span className="guest-text">{adult} Adult</span>
      <span className="guest-text">{children} Children</span>
      <span className="guest-text">{room} Room</span>
      
      <img className="up-and-down" src={UpAndDownIcon} alt="" />

      <div className={`guest-content ${!isOpen ? "open" : "hide"}`}>
        <div className="item">
          <span className="title">Adult</span>
          <div className="wrapper">
            <button
              onClick={() => setAdult(adult - 1)}
              className="minus"
              disabled={adult === 0 && true}
            >−</button>
            <input type="number" disabled className="input" value={adult} />
            <button
              onClick={() => setAdult(adult + 1)}
              className="plus"
            >+</button>
          </div>
        </div>
        <div className="item">
          <span className="title">Children</span>
          <div className="wrapper">
            <button
              onClick={() => setChildren(children - 1)}
              className="minus"
              disabled={children === 0 && true}
            >−</button>
            <input type="number" disabled className="input" value={children} />
            <button
              onClick={() => setChildren(children + 1)}
              className="plus"
            >+</button>
          </div>
        </div>
        <div className="item">
          <span className="title">Room</span>
          <div className="wrapper">
            <button
              onClick={() => setRoom(room - 1)}
              className="minus"
              disabled={room === 0 && true}
            >−</button>
            <input type="number" disabled className="input" value={room} />
            <button
              onClick={() => setRoom(room + 1)}
              className="plus"
            >+</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default GuestInput;
