import { compose } from "redux";
import { connect } from "react-redux";
import { withFormik } from "formik";

import RegistrationView from "./Registration.view.js";
import { signUpRequest } from "../../redux/actions/user.action.js";
import {
  isLoading,
  isRegisterStatus,
  getUserError
} from "../../redux/selectors/user.selector.js";
import { validateEmail, validatePassword, comparePassword } from "../../utils/validate.utils.js";
import { ERROR_VALIDATION } from "../../config/constants.js";

const validate = (values) => {
  const errors = {};
  const { email, password, confirmPassword, privacy } = values;

  if (!email) {
    errors.email = ERROR_VALIDATION.GENERAL_REQUIRED;
  } else if (!validateEmail(email)) {
    errors.email = ERROR_VALIDATION.EMAIL_FORMATED;
  }

  if (!password) {
    errors.password = ERROR_VALIDATION.GENERAL_REQUIRED;
  } else if (!validatePassword(password)) {
    errors.password = ERROR_VALIDATION.PASSWORD_FORMATED;
  }

  if (!confirmPassword) {
    errors.confirmPassword = ERROR_VALIDATION.GENERAL_REQUIRED;
  } else if (!validatePassword(confirmPassword)) {
    errors.confirmPassword = ERROR_VALIDATION.PASSWORD_FORMATED;
  } else if (!comparePassword(password, confirmPassword)) {
    errors.confirmPassword = ERROR_VALIDATION.PASSWORD_NOT_MATCHED;
  }

  if (privacy === false) {
    errors.privacy = ERROR_VALIDATION.PRIVACY_REQUIRED;
  }

  return errors;
};

const formikConfig = {
  mapPropsToValues: () => ({
    email: "",
    password: "",
    confirmPassword: "",
    username: "",
    privacy: false,
  }),
  validate: validate,
  handleSubmit: (values, { props }) => {
    const { signUpRequest } = props;
    const { email, password, username } = values;
    console.log('values', values);
    
    setTimeout(() => {
      signUpRequest({
        email,
        password,
        username
      });
    }, 1200);
  },
};

const mapStateToProps = (state) => ({
  isRegisterStatus: isRegisterStatus(state),
  userError: getUserError(state),
  isLoading: isLoading(state)
});

const mapDispatchToProps = (dispatch) => ({
  signUpRequest: (payload) => {
    dispatch(signUpRequest(payload));
  },
});
const enhance = compose(
  connect(mapStateToProps, mapDispatchToProps),
  withFormik(formikConfig)
);
export default enhance(RegistrationView);
