import storage from 'redux-persist/lib/storage';
import { persistReducer } from 'redux-persist';

import { USER } from "../../config/constants";

const initialState = {
  isLoading: false,
  error: null,
  isRegister: false
};

const UserReducer = (state = initialState, action) => {
  let user, error = null;

  switch (action.type) {
    case USER.SIGNIN_REQUEST:
      console.log('reducer', Date.now())
      return Object.assign({},
        {
          ...state,
          isLoading: true
        }
      );
    
    case USER.SIGNIN_SUCCESS:
      user = action.payload;
      return Object.assign({},
        { ...state,
          ...user,
          isLoading: false,
          error: null
        }
      );

    case USER.SIGNIN_FAILURE:
      error = action.payload;
      return Object.assign({},
        { ...state,
          error,
          isLoading: false
        }
      );

    case USER.SIGNUP_REQUEST:
      return Object.assign({},
        {
          ...state,
          isLoading: true
        }
      );

    case USER.SIGNUP_SUCCESS:
      user = action.payload;
      return Object.assign({},
        { ...state,
          isRegister: true,
          isLoading: false,
          error: null
        }
      );

    case USER.SIGNUP_FAILURE:
      error = action.payload;
      return Object.assign({},
        { ...state,
          error,
          isLoading: false
        }
      );
    
    default:
      return state;
  }
}

const userPersistConfig = {
  key: 'user',
  storage: storage,
  blacklist: ['error', 'isLoading', 'isRegister']
};

export default persistReducer(userPersistConfig, UserReducer);
