import React from "react";
import NotifyToast, { notify } from "react-notify-toast";

import { NOTIFICATION_TIME } from "../../config/constants";
// import PropTypes from 'prop-types';
import "./Notification.scss";

const Notification = (props) => {
  const { toastContainerClassName } = props;
  const toastOptions = {
    timeout: NOTIFICATION_TIME.FAST,
  };
  let toastContainerClass = ["notification-container"];
  toastContainerClassName && toastContainerClass.push(toastContainerClassName);

  return (
    <div className={toastContainerClass} onClick={notify.hide}>
      <NotifyToast options={toastOptions} />
    </div>
  );
};

Notification.propTypes = {
  // isShowErrorText: PropTypes.bool
};
Notification.defaultProps = {
  // isShowErrorText: true
};

export default Notification;
