import React from "react";

import "./SocialMedia.scss";

export default (props) => {
  const {
    text,
    containerClassName,
    wrapperClassName,
    socialIconClassName,
  } = props;
  const socialContainerClass = [
    "btn-social",
    containerClassName && containerClassName,
  ];
  const socialWrapperClass = ["wrapper", wrapperClassName && wrapperClassName];
  const socialIconClass = [socialIconClassName];

  return (
    <div className={socialContainerClass.join(" ")}>
      <button type="button" className={socialWrapperClass.join(" ")}>
        <div className={socialIconClass.join(" ")}>
          <div className="icon"></div>
        </div>
        <span className="social-text">{text}</span>
      </button>
    </div>
  );
};
