import React from "react";
import PropTypes from "prop-types";

import ErrorText from "../ErrorText/ErrorText.js";

import "./InputText.scss";
import errorIcon from "../../assets/images/error-icon.png";

const InputText = (props) => {
  const {
    touched,
    error,
    inputClass,
    containerClassName,
    inputClassName,
    icon,
    isShowErrorText,
    onChangeInputType,
    ...rest
  } = props;

  const containerClass = ["input-container"];
  const inputBoxClass = ["input-box"];
  const input = ["input"];
  const errorIconClass = "error-icon";

  containerClassName && containerClass.push(containerClassName);
  inputClassName && input.push(inputClassName);
  touched && error && inputBoxClass.push("border-error");
  touched && error && containerClass.push("input-container-error");

  return (
    <div className={containerClass.join(" ")}>
      <div className={inputBoxClass.join(" ")}>
        {icon && <img onClick={onChangeInputType} src={icon} className="input-icon" alt="" />}
        <input {...rest} className={input} />
        {touched && error && (
          <span className={errorIconClass}>
            <img src={errorIcon} alt="Error" />
          </span>
        )}
      </div>
      {error && touched && <ErrorText errorMessage={error} />}
    </div>
  );
};

InputText.propTypes = {
  isShowErrorText: PropTypes.bool,
};
InputText.defaultProps = {
  isShowErrorText: true,
};

export default InputText;
