import React from "react";
// import { connect } from "react-redux";
import { compose } from "recompose";

// import { getUserInfo } from "../../redux/selectors/user.selector"
import PrimaryHeader from "./PrimaryHeader";
import SecondaryHeader from "./SecondaryHeader";
import { LIST_SECONDARY_HEADER } from "../../config/constants";

const HeaderHOC = (WrappedComponent) => {
  return class extends React.Component {
    render() {
      const { location, user } = this.props;
      console.log('user', user);
      if(LIST_SECONDARY_HEADER.includes(location && location.pathname)) {
        return <SecondaryHeader user={user} />
      } else {
        return <WrappedComponent user={user} />;
      }
    }
  };
}

const EnhancedHeaderComponent = compose()(HeaderHOC);

export default EnhancedHeaderComponent(PrimaryHeader);
