import axios from 'axios';
import { environment } from '../environment';
export default class APIService {
  constructor() {
    axios.defaults.baseURL = `${environment.api.domain}${environment.api.prefixApi}/`;
    // this.setCommonHeader()
    this.setInterceptors();
  }

  setCommonHeader() {
    axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
    axios.defaults.headers.common['Access-Control-Allow-Methods'] = 'GET,POST,OPTIONS';
    axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Content-Type';
  }

  setInterceptors() {
    axios.interceptors.request.use((request) => {
      if (!request.url.includes(environment.line)) {
        const token = localStorage.getItem('ACCESS-TOKEN');
        if (token) {
          request.headers['Authorization'] = `Bearer ${token}`;
        }
      }
      return request;
    }, (error) => {
      return Promise.reject(error);
    });
    axios.interceptors.response.use(
      (response) => response,
      (error) => {
        // handle error
        if ( error.response && error.response.status === 401 ) {
          localStorage.removeItem('ACCESS-TOKEN');
          localStorage.removeItem('LINE-TOKEN');
          // location.reload();
          return Promise.reject( error );
        }
      });
  }

  createURL(uri) {
    let paramsUrl;
    if (typeof uri[uri.length - 1] !== 'string') {
      paramsUrl = uri.pop();
      let url = uri.join('/');
      Object.keys(paramsUrl).forEach(x => {
        url = url.replace(`:${x}`, paramsUrl[x]);
      });
      return url;
    } else {
      return uri.join('/');
    }
  }

  get(uri, options = {}) {
    console.log('uri', uri);
    console.log('this', this);
    return new Promise((resolve, reject) => {
      axios.get(`${environment.api.domain}${environment.api.prefixApi}/${uri}`, {
        params: options,
      })
      .then((resp) => {
        // if(resp && resp.data) {
          resolve(resp.data);
        // } else {
        //   reject({
        //     message: "Data doesn't exist"
        //   })
        // }
      })
      .catch((err) => {
        reject(err);
      });
    });
  }

  post(uri, data = {}, moreConfigs = {}) {
    return new Promise((resolve, reject) => {
      axios.post(this.createURL(uri), data, moreConfigs)
        .then((resp) => {
          resolve(resp.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  put(uri, data = {}) {
    return new Promise((resolve, reject) => {
      axios.put(this.createURL(uri), data)
        .then((resp) => {
          resolve(resp.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  delete(uri) {
    return new Promise((resolve, reject) => {
      axios.delete(this.createURL(uri))
        .then((resp) => {
          resolve(resp.data);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  _form(data) {
    const formData = new FormData();
    for (const param in data) {
      if (data.hasOwnProperty(param)) {
        if (Array.isArray(data[param]) && data[param].length > 0) {
          data[param].forEach((item, index) => {
            formData.append(`${param}[${index}]`, item);
          });
        } else {
          formData.append(param, data[param]);
        }
      }
    }
    return formData;
  }

  multipeRequests(apiReqs) {
    return new Promise( ( resolve, reject ) => {
      axios.all(apiReqs)
        .then((resp) => {
          resolve(resp.map((v) => v.data ));
        })
        .catch((err) => reject(err));
    });
  }
}
