import React from "react";

export default React.memo((props) => {
  const { title, subTitle, titleClassName, subTitleClassName } = props;

  const formTitleClass = ['form-title', titleClassName && titleClassName];
  const formSubTitleClass = ['form-subtitle', subTitleClassName && subTitleClassName];

  return (
    <React.Fragment>
      <h2 className={formTitleClass.join(' ')}>{title}</h2>
      <p className={formSubTitleClass.join(' ')}>
        {subTitle}
      </p>
    </React.Fragment>
  );
});
