import React from 'react';

export default (props) => {
  const { text, id, name, error, touched, ...rest } = props;
  const checkBoxClass = ["checkbox", error  && touched && "error-checkbox"];

  return (
    <div className="checkbox-container">
      <input {...rest} name={name} type="checkbox" id={id} className={checkBoxClass.join(' ')} />
      <label htmlFor={id} className="label">{text}</label>
    </div>
  );
}
