import React from "react";

import loadingIcon from "../../assets/images/loading-white.svg";
import "./SubmitButton.scss";

export default React.memo((props) => {
  const { text, isLoading, buttonClassName, containerClassName } = props;
  const btnClass = ["btn", "btn-submit", buttonClassName && buttonClassName];
  const containerClass = ["btn-container", containerClassName && containerClassName]

  console.log('loading', isLoading);
  return (
    <div className={containerClass.join(' ')}>
      <button type="submit" className={btnClass.join(" ")} name="submit">
        {text}
      </button>
      {isLoading && <img src={loadingIcon} className="loading" alt="" />}
    </div>
  );
});
