import { compose } from "redux";
import { connect } from "react-redux";
import { withFormik } from "formik";
import { push } from 'react-router-redux';

import LoginView from "./Login.view.js";
import { signInRequest } from "../../redux/actions/user.action.js";
import {
  isLoading,
  getUserInfo,
  getUserError
} from "../../redux/selectors/user.selector.js";
import { validateEmail, validatePassword } from "../../utils/validate.utils.js";
import { ERROR_VALIDATION } from "../../config/constants.js";
import routesString from "../../config/route.string.js";

const validate = (values) => {
  const errors = {};

  if (!values.email) {
    errors.email = ERROR_VALIDATION.GENERAL_REQUIRED;
  } else if (!validateEmail(values.email)) {
    errors.email = ERROR_VALIDATION.EMAIL_FORMATED;
  }

  if (!values.password) {
    errors.password = ERROR_VALIDATION.GENERAL_REQUIRED;
  } else if (!validatePassword(values.password)) {
    errors.password = ERROR_VALIDATION.PASSWORD_FORMATED;
  }

  return errors;
};

const formikConfig = {
  mapPropsToValues: () => ({
    email: "",
    password: "",
  }),
  validate: validate,
  handleSubmit: async(values, { props }) => {
    const { signInRequest } = props;
    const { email, password } = values;
    setTimeout(() => {
      signInRequest({
        email,
        password,
      });
    }, 1200);
  },
};

const mapStateToProps = (state) => ({
  user: getUserInfo(state),
  userError: getUserError(state),
  isLoading: isLoading(state)
});

const mapDispatchToProps = (dispatch) => ({
  signInRequest: (payload) => {
    dispatch(signInRequest(payload));
  },
  goToHomePage: () => {
    console.log("run goToHomePage")
    dispatch(push({pathname: routesString.home}))
  }
});

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withFormik(formikConfig),
)(LoginView);
