import { put, takeLatest } from "redux-saga/effects";

import apiService from "../services/api.js";
import { USER } from "../config/constants.js";
import { API_ENDPOINTS, API_STATUS } from "../config/api.js";

const api = new apiService();

export function* signIn({ payload }) {
  console.log('saga', Date.now())

  const { email, password } = payload;
  const body = { email, password };
  try {
    let response = yield api.post([API_ENDPOINTS.USER_SIGNIN], body);
    if (response.responseCode === API_STATUS.OK) {
      yield put({ type: USER.SIGNIN_SUCCESS, payload: response });
    } else {
      yield put({ type: USER.SIGNIN_FAILURE, payload: response });
    }
  } catch (error) {
    yield put({ type: USER.SIGNIN_FAILURE, payload: error });
  }
}

export function* watchSignIn() {
  yield takeLatest(USER.SIGNIN_REQUEST, signIn);
}


export function* signUp({ payload}) {
  const { email, password, username } = payload;
  const body = { email, password, username };
  try {
    let response = yield api.post([API_ENDPOINTS.USER_SIGNUP], body);
    console.log('response', response);
    if (response.responseCode === API_STATUS.OK) {
      yield put({ type: USER.SIGNUP_SUCCESS, payload: response });
    } else {
      yield put({ type: USER.SIGNUP_FAILURE, payload: response });
    }
  } catch (error) {
    yield put({ type: USER.SIGNIN_FAILURE, payload: error });
  }
}

export function* watchSignUp() {
  yield takeLatest(USER.SIGNUP_REQUEST, signUp);
}
