import React from "react";

import "./HotelPlanItem.scss";
import Place01 from "../../assets/images/place01.jpg";

const PlanItem = React.memo((props) => {
  const { item, key } = props;
  return (
    <div className="card" key={key}>
      <div className="card-image" style={{backgroundImage: `url(${Place01})`}}></div>
      <div className="card-content">
        <h3 className="title">{item.title}</h3>
        <p className="subtitle">{item.subtitle}</p>
        <p className="price">Starting from {item.currency} {item.price}</p>
        <div className="review">
        <div className="review-score">{item.reviewScore}</div>
          <div className="review-content">
            <span className="title">{item.reviewText}</span>
            <span className="text">{item.reviewTotal} reviews</span>
          </div>
        </div>
      </div>
    </div>
  );
});

export default PlanItem;
