import { createSelector } from "reselect";
import { get } from "lodash";

let rootSelector = (state) => state.user;

export const getUserInfo = createSelector(rootSelector, (userInfo) =>
  get(userInfo, "userInfo")
);
export const getUserError = createSelector(rootSelector, (error) =>
  get(error, "error")
);
export const isLoading = createSelector(rootSelector, (isLoading) =>
  get(isLoading, "isLoading")
);
export const isRegisterStatus = createSelector(rootSelector, (isRegister) =>
  get(isRegister, "isRegister")
);
