import React from "react";
import PlacesAutocomplete from "react-places-autocomplete";

import "./style.scss";
import PlaceIcon from "../../assets/images/place-icon.png";
import PlaceHolderIcon from "../../assets/images/placeholder.svg";
// import SearchIcon from "../../assets/images/search-icon.png";

class PlaceAutocomplete extends React.Component {
  constructor(props) {
    super(props);
    this.state = { address: "" };
  }

  handleChange = (address) => {
    this.setState({ address });
  };

  handleSelect = (address) => {
    this.setState({ address });
    // geocodeByAddress(address)
    //   .then((results) => getLatLng(results[0]))
    //   .then((latLng) => console.log("Success", latLng))
    //   .catch((error) => console.error("Error", error));
  };

  render() {
    return (
      <PlacesAutocomplete
        value={this.state.address}
        onChange={this.handleChange}
        onSelect={this.handleSelect}
      >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
          <div className="address-wrapper">
            <img className="typing-icon" src={PlaceIcon} alt="" />
            <div className="searchbox">
              <input
                {...getInputProps({
                  placeholder: "Where are you going?",
                  className: "location-search-input",
                })}
              />
            </div>
            {/* <img className="search-icon" src={SearchIcon} alt="" /> */}

            <div className="autocomplete-dropdown-container">
              {loading && <div className="search-loading">Loading...</div>}

              {suggestions.map((suggestion) => {
                const className = suggestion.active
                  ? "suggestion-item--active"
                  : "suggestion-item";
                // inline style for demonstration purpose
                // const style = suggestion.active
                //   ? { backgroundColor: "#ececec", cursor: "pointer", fontSize: "14px" }
                //   : { backgroundColor: "#ffffff", cursor: "pointer" };
                return (
                  <div
                    {...getSuggestionItemProps(suggestion, {
                      className,
                    })}
                    key={suggestion.id}
                  >
                    <img className="placeholder-icon" src={PlaceHolderIcon} alt="" />
                    <div className="content">
                      <p className="title">{suggestion.formattedSuggestion.mainText}</p>
                      <span className="desc">{suggestion.formattedSuggestion.secondaryText}</span>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </PlacesAutocomplete>
    );
  }
}

export default PlaceAutocomplete;
